"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import CashflowChart from "./components/cashflowChart.jsx";
import { useCashflowData } from "./components/useCashflowData";
import KPICards from "./components/kpiCards";
import IncomePieChart from "./components/incomePieChart";
import ExpensePieChart from "./components/expensePieChart";

export default function Page() {
  const { transactions: fetchedTransactions, chartData, loading } = useCashflowData();
  const [transactions, setTransactions] = useState([]);

  useEffect(() => {
    setTransactions(fetchedTransactions || []);
  }, [fetchedTransactions]);

  // KPI Calculations
  const totalIncome = chartData?.reduce((a, r) => a + (r.income || 0), 0) || 0;
  const totalExpense = chartData?.reduce((a, r) => a + (r.expense || 0), 0) || 0;

  const beginningBalance = transactions.length ? transactions[0].balance : 0;
  const runningBalance = transactions.length
    ? transactions[transactions.length - 1].balance
    : 0;

  const lastMonth = chartData.length ? chartData[chartData.length - 1] : null;
  const monthlySurplus = lastMonth
    ? (lastMonth.income || 0) - (lastMonth.expense || 0)
    : 0;

  const totals = {
    beginningBalance,
    totalIncome,
    totalExpense,
    runningBalance,
    monthlySurplus,
  };

  if (loading) {
    return (
      <div className="flex min-h-screen items-center justify-center bg-slate-50">
        <p className="text-sm text-slate-500 animate-pulse">
          Loading financial dashboard…
        </p>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-emerald-50 via-white to-emerald-100 px-3 py-4 lg:px-6 lg:py-6">
      {/* removed max-w to use full width */}
      <div className="flex w-full flex-col gap-6">

        {/* HEADER */}
        <header className="flex flex-col justify-between gap-4 border-b border-slate-200 pb-4 md:flex-row md:items-center">
          <div className="space-y-1">
            <h1 className="text-3xl font-semibold tracking-tight text-slate-900 md:text-4xl">
              Cash Flow Overview
            </h1>
            <p className="text-sm text-slate-500">
              Barangay financial performance summary for fiscal year 2025.
            </p>
          </div>

          <div className="flex flex-wrap items-center gap-3">
            <div className="rounded-full bg-white px-3 py-1 text-xs font-medium text-slate-500 shadow-sm border border-slate-200">
              Last updated:{" "}
              <span className="font-semibold text-slate-700">
                {new Date().toLocaleDateString()}
              </span>
            </div>
            <div className="flex items-center gap-2 text-xs text-emerald-600">
              <span className="h-2 w-2 rounded-full bg-emerald-500 animate-pulse" />
              Live connection to Supabase
            </div>
          </div>
        </header>

        {/* KPI SECTION */}
        <section className="space-y-4">
          <div>
            <h2 className="text-sm font-semibold uppercase tracking-wide text-slate-500">
              Key Metrics
            </h2>
            <p className="text-xs text-slate-400">
              High-level indicators for income, expense, and cash position.
            </p>
          </div>
          <KPICards totals={totals} />
        </section>

        {/* ANALYTICS GRID */}
        {/* xl: 4 columns -> main chart uses 3, breakdown column uses 1 */}
        <section className="grid grid-cols-1 xl:grid-cols-4 gap-5">

          {/* LEFT SIDE – MAIN CHART (bigger, 3/4 width) */}
          <div className="xl:col-span-3">
            <Card className="rounded-2xl border border-emerald-300 shadow-xl bg-white/90 backdrop-blur-sm">
              <CardHeader className="border-b bg-gradient-to-r from-emerald-100 to-emerald-50">
                <CardTitle className="text-xl font-semibold text-emerald-800">
                  Monthly Income vs Expense
                </CardTitle>
              </CardHeader>

              <CardContent className="p-5">
                <div className="h-[480px]">
                  <CashflowChart data={chartData} />
                </div>
              </CardContent>
            </Card>
          </div>

          {/* RIGHT SIDE – STACKED BREAKDOWNS */}
          <div className="flex flex-col gap-5">

            {/* Income Breakdown */}
            <Card className="rounded-xl border border-emerald-200 shadow-sm bg-white">
              <CardHeader className="bg-emerald-600 py-2 rounded-t-xl">
                <CardTitle className="text-white text-sm font-semibold">
                  Income Breakdown
                </CardTitle>
              </CardHeader>

              <CardContent className="p-4">
                <div className="h-[180px] flex justify-center items-center">
                  <IncomePieChart transactions={transactions} />
                </div>
              </CardContent>
            </Card>

            {/* Expense Breakdown */}
            <Card className="rounded-xl border border-red-200 shadow-sm bg-white">
              <CardHeader className="bg-red-600 py-2 rounded-t-xl">
                <CardTitle className="text-white text-sm font-semibold">
                  Expense Breakdown
                </CardTitle>
              </CardHeader>

              <CardContent className="p-4">
                <div className="h-[180px] flex justify-center items-center">
                  <ExpensePieChart transactions={transactions} />
                </div>
              </CardContent>
            </Card>


          </div>
        </section>
      </div>
    </div>
  );
}
